import React, { useState } from 'react';
import { StockItem } from './types';

// MUI Imports
import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogContentText,
    DialogActions,
    TextField,
    Button,
    Typography,
    Box,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    FormHelperText // For error messages on Select
} from '@mui/material';

interface ResolveIssueModalProps {
  item: StockItem | null; // Allow null
  onSubmit: (outcome: string, note?: string) => void;
  onClose: () => void;
  open: boolean; // Control visibility
}

const RESOLUTION_OUTCOMES = [
    "Item Accepted / Kept As Is",
    "Item Repaired / Refurbished",
    "Partial Refund Received",
    "Returned to Supplier",
    "Disposed Of",
    "Other" // Allows adding notes for specifics
];

const ResolveIssueModal: React.FC<ResolveIssueModalProps> = ({ item, onSubmit, onClose, open }) => {
  const [outcome, setOutcome] = useState('');
  const [note, setNote] = useState('');
  const [error, setError] = useState('');

  // Clear state when modal closes or item changes
  React.useEffect(() => {
    if (!open) {
      setOutcome('');
      setNote('');
      setError('');
    }
  }, [open]);

  const handleSubmit = () => {
    if (!outcome) {
      setError('Please select a resolution outcome.');
      return;
    }
    onSubmit(outcome, note.trim() || undefined); // Pass note only if it has content
    // Parent handles closing via 'open' prop
  };

  // Don't render if not open or no item
  if (!open || !item) {
    return null;
  }

  return (
    <Dialog open={open} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle>Resolve Issue for: {item.productName} - {item.deliveryName}</DialogTitle>
      <DialogContent>
        <DialogContentText sx={{ mb: 2 }}>
          For item: <Typography component="span" sx={{ fontStyle: 'italic', fontWeight: 'medium' }}>{item.productName} - {item.deliveryName}</Typography>
        </DialogContentText>

        {/* Display existing issue description for context */} 
        {item.issueDescription && (
          <Box sx={{ mb: 2, p: 1.5, background: (theme) => theme.palette.grey[100], border: (theme) => `1px solid ${theme.palette.grey[300]}`, borderRadius: 1 }}>
            <Typography variant="body2" sx={{ fontWeight: 'bold', mb: 0.5 }}>Initial Issue:</Typography>
            <Typography variant="body2">{item.issueDescription}</Typography>
          </Box>
        )}

        <FormControl fullWidth margin="normal" required error={!!error}> 
          <InputLabel id="resolution-outcome-label">Resolution Outcome</InputLabel>
          <Select
            labelId="resolution-outcome-label"
            id="resolutionOutcome"
            value={outcome}
            label="Resolution Outcome" // Required for label positioning
            onChange={(e) => {
              setOutcome(e.target.value);
              if (error) setError(''); // Clear error on change
            }}
          >
            <MenuItem value="" disabled>
              <em>-- Select an Outcome --</em>
            </MenuItem>
            {RESOLUTION_OUTCOMES.map(opt => (
              <MenuItem key={opt} value={opt}>{opt}</MenuItem>
            ))}
          </Select>
          {error && <FormHelperText>{error}</FormHelperText>} 
        </FormControl>

        <TextField
          margin="normal"
          id="resolutionNotes"
          label="Resolution Notes (Optional)"
          type="text"
          fullWidth
          multiline
          rows={3}
          variant="outlined"
          value={note}
          onChange={(e) => setNote(e.target.value)}
          placeholder="e.g., Supplier issued refund, repaired screen..."
        />
      </DialogContent>
      <DialogActions sx={{ px: 3, pb: 2 }}>
        <Button onClick={onClose} color="secondary">
          Cancel
        </Button>
        <Button onClick={handleSubmit} variant="contained" color="primary">
          Save Resolution
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default ResolveIssueModal;