import React, { useState } from 'react';
import { StockItem } from './types';
// MUI Imports
import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogContentText, // For descriptive text
    DialogActions,
    TextField,
    Button,
    Typography,
    Box // For layout if needed
} from '@mui/material';

interface AddUpdateModalProps {
  item: StockItem | null; // Allow null to handle cases where it might not be set yet
  onSubmit: (note: string) => void;
  onClose: () => void;
  open: boolean; // Control modal visibility from parent
}

const AddUpdateModal: React.FC<AddUpdateModalProps> = ({ item, onSubmit, onClose, open }) => {
  const [note, setNote] = useState('');
  const [error, setError] = useState('');

  // Clear state when modal closes or item changes
  React.useEffect(() => {
    if (!open) {
        setNote('');
        setError('');
    }
  }, [open]);

  const handleSubmit = () => {
    if (!note.trim()) {
      setError('Please enter an update note.');
      return;
    }
    onSubmit(note);
    // Parent component should handle closing via the 'open' prop
  };

  // Don't render anything if item is null or modal is not open
  if (!item || !open) {
      return null;
  }

  return (
    <Dialog open={open} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle>Add Issue Update</DialogTitle>
      <DialogContent>
        <DialogContentText sx={{ mb: 2 }}>
          For item: <Typography component="span" sx={{ fontStyle: 'italic', fontWeight: 'medium' }}>{item.productName} - {item.deliveryName}</Typography>
        </DialogContentText>

        {/* Display existing issue description for context */} 
        {item.issueDescription && (
          <Box sx={{ mb: 2, p: 1.5, background: (theme) => theme.palette.grey[100], border: (theme) => `1px solid ${theme.palette.grey[300]}`, borderRadius: 1 }}>
            <Typography variant="body2" sx={{ fontWeight: 'bold', mb: 0.5 }}>Initial Issue:</Typography>
            <Typography variant="body2">{item.issueDescription}</Typography>
          </Box>
        )}

        <TextField
          autoFocus
          required
          margin="dense"
          id="updateNote"
          label="Update Note"
          type="text"
          fullWidth
          multiline
          rows={4}
          variant="outlined"
          value={note}
          onChange={(e) => {
            setNote(e.target.value);
            if (error) setError(''); // Clear error on type
          }}
          error={!!error} // Convert error message string to boolean
          helperText={error} // Display error message below TextField
          placeholder="e.g., Contacted seller, awaiting response..."
        />
      </DialogContent>
      <DialogActions sx={{ px: 3, pb: 2 }}>
        <Button onClick={onClose} color="secondary">
          Cancel
        </Button>
        <Button onClick={handleSubmit} variant="contained" color="primary">
          Save Update Note
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AddUpdateModal;