import React, { useState, useEffect, useCallback, useRef } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  Tooltip,
  IconButton,
  Box,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  FormHelperText,
} from '@mui/material';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import { StockItem, PurchaseStatus, Destination, User } from './types';
// Ensure SelectChangeEvent is imported correctly
import { SelectChangeEvent } from '@mui/material/Select';

const InfoTooltip = ({ text }: { text: string }) => (
  <Tooltip title={text} arrow>
    <IconButton size="small" sx={{ ml: 0.5, verticalAlign: 'middle' }}>
      <InfoOutlinedIcon fontSize="inherit" />
    </IconButton>
  </Tooltip>
);

interface AddItemFormProps {
  open: boolean;
  onClose: () => void;
  onSubmit: (
    // This Omit list might need updating based on your StockItem definition
    data: Omit<
      StockItem,
      'id' | 'currentStatus' | 'dateDelivered' | 'processorNotes' | 'issueDescription' | 'lastModified' | 'lastModifiedBy' | 'activityLog' | 'isLate'
    >
  ) => void;
  itemToEdit?: StockItem | null;
  user: User | null; // Add user prop
  orgId?: string; // Add optional orgId prop
}

const AddItemForm: React.FC<AddItemFormProps> = ({ open, onClose, onSubmit, itemToEdit, user, orgId }) => {
  const isEditing = !!itemToEdit;
  const firstInputRef = useRef<HTMLInputElement>(null);

  const getDefaultFormData = useCallback(() => ({
    purchaseStatus: itemToEdit?.purchaseStatus || 'Purchased' as PurchaseStatus,
    deliveryName: itemToEdit?.deliveryName || '',
    productName: itemToEdit?.productName || '',
    quantity: itemToEdit?.quantity || '' as number | '',
    purchasePrice: itemToEdit?.pricePerItem || '' as number | '', // Map from pricePerItem
    salesPrice: itemToEdit?.salesPrice || '' as number | '', // Ensure salesPrice exists in StockItem or handle null/undefined
    orderNumber: itemToEdit?.orderNumber || '',
    orderDate: (itemToEdit?.orderDate || new Date().toISOString().split('T')[0]),
    supplier: itemToEdit?.seller || '', // Map from seller
    isVatRegistered: itemToEdit?.isVatRegistered || 'Unknown' as 'Yes' | 'No' | 'Unknown',
    destination: itemToEdit?.destination || '' as Destination,
    asinSku: itemToEdit?.asinSku || '',
    acquisitionNotes: itemToEdit?.acquisitionNotes || '',
    isFlagged: itemToEdit?.isFlagged || false, // Keep as boolean in state
  }), [itemToEdit]);

  const [formData, setFormData] = useState(getDefaultFormData());
  const [errors, setErrors] = useState<Record<string, string>>({});

  useEffect(() => {
    if (open) {
      setFormData(getDefaultFormData());
      setErrors({});
      // Focus the first input when the dialog opens
      setTimeout(() => firstInputRef.current?.focus(), 100);
    }
  }, [itemToEdit, open, getDefaultFormData]);

  // Correctly typed handleChange for both TextField and Select
  const handleChange = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement> | SelectChangeEvent<any>) => {
    const target = event.target;
    const name = target.name as keyof typeof formData; // Assert name exists in formData keys
    let value: string | boolean | number | PurchaseStatus | Destination | 'Yes' | 'No' | 'Unknown' = target.value;

    // Handle specific field types
    if (name === 'isFlagged') {
      // Convert string from Select back to boolean
      value = target.value === 'true';
    } else if (target instanceof HTMLInputElement && target.type === 'checkbox') {
       // Handle checkboxes if any are added in the future
       value = target.checked;
    }

    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));

    if (errors[name]) {
      setErrors((prev) => {
        const newErrors = { ...prev };
        delete newErrors[name];
        return newErrors;
      });
    }
  };


  const validateForm = (): boolean => {
    const newErrors: Record<string, string> = {};
    if (!formData.deliveryName.trim()) newErrors.deliveryName = 'Delivery Name is required.';
    if (!formData.productName.trim()) newErrors.productName = 'Product Name is required.';
    if (formData.quantity === '' || Number(formData.quantity) <= 0) newErrors.quantity = 'Quantity must be a positive number.';
    if (formData.purchasePrice === '' || Number(formData.purchasePrice) < 0) newErrors.purchasePrice = 'Purchase Price must be zero or positive.';
    // Assume salesPrice is optional or check if required
    if (formData.salesPrice !== '' && Number(formData.salesPrice) < 0) newErrors.salesPrice = 'Sales Price cannot be negative.';
    if (!formData.orderDate) newErrors.orderDate = 'Order Date is required.';
    if (!formData.supplier.trim()) newErrors.supplier = 'Supplier is required.';

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (event: React.FormEvent) => {
    event.preventDefault();
    if (validateForm()) {
      // Map form data back to StockItem structure for submission
      const submitData = {
        ...formData,
        quantity: Number(formData.quantity),
        pricePerItem: Number(formData.purchasePrice), // Map back
        salesPrice: formData.salesPrice === '' ? null : Number(formData.salesPrice), // Handle empty string for optional number field
        seller: formData.supplier, // Map back
        // Remove fields specific to the form state but not StockItem structure
        // purchasePrice is removed because pricePerItem is used
        // supplier is removed because seller is used
      };
      // Remove the properties that were mapped
      delete (submitData as any).purchasePrice;
      delete (submitData as any).supplier;


      // Type assertion to satisfy onSubmit prop - Ensure this mapping is correct!
      onSubmit(submitData as Omit<StockItem, 'id' | 'currentStatus' | 'dateDelivered' | 'processorNotes' | 'issueDescription' | 'lastModified' | 'lastModifiedBy' | 'activityLog' | 'isLate'>);
      onClose(); // Close form on successful submission
    }
  };


  return (
    <Dialog open={open} onClose={onClose} maxWidth="md" fullWidth>
      <DialogTitle>{isEditing ? 'Edit Stock Item' : 'Add New Stock Item'}</DialogTitle>
      <DialogContent>
        <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
          {/* Flexbox container for layout */}
          <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 2, mt: 1 }}>
            {/* Delivery Name (sm: 50% width) */}
            <Box sx={{ flexBasis: { xs: '100%', sm: 'calc(50% - 8px)' }, flexGrow: 1 }}>
              <TextField
                inputRef={firstInputRef}
                autoFocus
                margin="dense"
                name="deliveryName"
                label="Delivery Name"
                type="text"
                fullWidth
                value={formData.deliveryName}
                onChange={handleChange}
                error={!!errors.deliveryName}
                helperText={errors.deliveryName || 'Group items arriving together (e.g., eBay Batch Apr 16)'}
                InputProps={{
                  endAdornment: <InfoTooltip text="Group items arriving together with a unique name. Helps find them later. Example: eBay Batch Apr 16" />,
                }}
                required
              />
            </Box>
            {/* Product Name (sm: 50% width) */}
            <Box sx={{ flexBasis: { xs: '100%', sm: 'calc(50% - 8px)' }, flexGrow: 1 }}>
              <TextField
                margin="dense"
                name="productName"
                label="Product Name"
                type="text"
                fullWidth
                value={formData.productName}
                onChange={handleChange}
                error={!!errors.productName}
                helperText={errors.productName || 'The common name of the item (e.g., Blue Widget Model X)'}
                InputProps={{
                  endAdornment: <InfoTooltip text="The common name of the item. Example: Blue Widget Model X" />,
                }}
                required
              />
            </Box>
            {/* Quantity (sm: ~25% width - adapting from original sm=3) */}
            <Box sx={{ flexBasis: { xs: 'calc(50% - 8px)', sm: 'calc(25% - 12px)' }, flexGrow: 1 }}>
              <TextField
                margin="dense"
                name="quantity"
                label="Quantity"
                type="number"
                fullWidth
                value={formData.quantity}
                onChange={handleChange}
                error={!!errors.quantity}
                helperText={errors.quantity || 'How many units?'}
                InputProps={{
                  inputProps: { min: 1 },
                  endAdornment: <InfoTooltip text="How many units of this item are included? Example: 50" />,
                }}
                required
              />
            </Box>
            {/* Purchase Price (sm: ~25% width) */}
            <Box sx={{ flexBasis: { xs: 'calc(50% - 8px)', sm: 'calc(25% - 12px)' }, flexGrow: 1 }}>
              <TextField
                margin="dense"
                name="purchasePrice"
                label="Purchase Price"
                type="number"
                fullWidth
                value={formData.purchasePrice}
                onChange={handleChange}
                error={!!errors.purchasePrice}
                helperText={errors.purchasePrice || 'Cost per single unit (excl. VAT)'}
                InputProps={{
                  inputProps: { min: 0, step: '0.01' },
                  endAdornment: <InfoTooltip text="The cost for one single unit, excluding VAT. Example: 10.42" />,
                }}
                required
              />
            </Box>
            {/* Sales Price (sm: ~25% width) */}
            <Box sx={{ flexBasis: { xs: 'calc(50% - 8px)', sm: 'calc(25% - 12px)' }, flexGrow: 1 }}>
              <TextField
                margin="dense"
                name="salesPrice"
                label="Sales Price (Optional)"
                type="number"
                fullWidth
                value={formData.salesPrice}
                onChange={handleChange}
                error={!!errors.salesPrice}
                helperText={errors.salesPrice || 'Optional: The target selling price for one single unit, including VAT. Example: 19.99'}
                InputProps={{
                  inputProps: { min: 0, step: '0.01' },
                  endAdornment: <InfoTooltip text="Optional: The target selling price for one single unit, including VAT. Example: 19.99" />,
                }}
              />
            </Box>
            {/* Order Number (sm: 50% width) */}
            <Box sx={{ flexBasis: { xs: '100%', sm: 'calc(50% - 8px)' }, flexGrow: 1 }}>
              <TextField
                margin="dense"
                label="Order Number (Optional)"
                name="orderNumber"
                value={formData.orderNumber}
                onChange={handleChange}
                fullWidth
                error={!!errors.orderNumber}
                helperText={errors.orderNumber}
              />
            </Box>
            {/* Order Date (sm: 50% width) */}
            <Box sx={{ flexBasis: { xs: '100%', sm: 'calc(50% - 8px)' }, flexGrow: 1 }}>
              <TextField
                margin="dense"
                name="orderDate"
                label="Order Date"
                type="date"
                fullWidth
                value={formData.orderDate}
                onChange={handleChange}
                error={!!errors.orderDate}
                helperText={errors.orderDate || 'The date the order was placed.'}
                InputLabelProps={{ shrink: true }}
                InputProps={{
                  endAdornment: <InfoTooltip text="The date the item was purchased or the return initiated." />,
                }}
                required
              />
            </Box>
            {/* Supplier (sm: ~25% width) */}
            <Box sx={{ flexBasis: { xs: 'calc(50% - 8px)', sm: 'calc(25% - 12px)' }, flexGrow: 1 }}>
              <TextField
                margin="dense"
                label="Supplier"
                name="supplier"
                value={formData.supplier}
                onChange={handleChange}
                fullWidth
                error={!!errors.supplier}
                helperText={errors.supplier || 'Who did you buy this from? (e.g., eBay Seller name, Amazon)'}
                InputProps={{
                  endAdornment: <InfoTooltip text="The name of the seller or store you purchased from. Example: JohnDoe123 (eBay)" />,
                }}
                required
              />
            </Box>
            {/* ASIN/SKU (sm: 50% width) */}
            <Box sx={{ flexBasis: { xs: '100%', sm: 'calc(50% - 8px)' }, flexGrow: 1 }}>
              <TextField
                margin="dense"
                label="ASIN/SKU (Optional)"
                name="asinSku"
                value={formData.asinSku}
                onChange={handleChange}
                fullWidth
                error={!!errors.asinSku}
                helperText={errors.asinSku || 'Optional: Amazon ASIN or your internal SKU'}
                InputProps={{
                  endAdornment: <InfoTooltip text="Optional: Amazon Standard Identification Number (ASIN) or your internal Stock Keeping Unit (SKU). Example: B076MX3KCY" />,
                }}
              />
            </Box>
            {/* Destination (sm: 50% width) */}
            <Box sx={{ flexBasis: { xs: '100%', sm: 'calc(50% - 8px)' }, flexGrow: 1 }}>
              <FormControl fullWidth margin="dense" error={!!errors.destination}>
                <InputLabel id="destination-label">Destination (Optional)</InputLabel>
                <Select
                  labelId="destination-label"
                  label="Destination (Optional)"
                  name="destination"
                  value={formData.destination}
                  onChange={handleChange}
                >
                  <MenuItem value="">Select Destination</MenuItem>
                  <MenuItem value="Warehouse A">Warehouse A</MenuItem>
                  <MenuItem value="Warehouse B">Warehouse B</MenuItem>
                  <MenuItem value="Prep Center">Prep Center</MenuItem>
                </Select>
                {errors.destination && <FormHelperText>{errors.destination}</FormHelperText>}
              </FormControl>
            </Box>
            {/* VAT Registered? (sm: 50% width) */}
            <Box sx={{ flexBasis: { xs: '100%', sm: 'calc(50% - 8px)' }, flexGrow: 1 }}>
              <FormControl fullWidth margin="dense" error={!!errors.isVatRegistered}>
                <InputLabel id="vat-registered-label">VAT Registered? (Optional)</InputLabel>
                <Select
                  labelId="vat-registered-label"
                  label="VAT Registered? (Optional)"
                  name="isVatRegistered"
                  value={formData.isVatRegistered}
                  onChange={handleChange}
                >
                  <MenuItem value="Unknown">Unknown</MenuItem>
                  <MenuItem value="Yes">Yes</MenuItem>
                  <MenuItem value="No">No</MenuItem>
                </Select>
                {errors.isVatRegistered && <FormHelperText>{errors.isVatRegistered}</FormHelperText>}
              </FormControl>
            </Box>
            {/* Is Flagged? (sm: 50% width) */}
            <Box sx={{ flexBasis: { xs: '100%', sm: 'calc(50% - 8px)' }, flexGrow: 1 }}>
              <FormControl fullWidth margin="dense" error={!!errors.isFlagged}>
                <InputLabel id="is-flagged-label">Flag Item?</InputLabel>
                <Select
                  labelId="is-flagged-label"
                  label="Flag Item?"
                  name="isFlagged"
                  value={formData.isFlagged ? 'true' : 'false'} // Convert boolean to string for Select
                  onChange={handleChange}
                >
                  <MenuItem value="false">No</MenuItem>
                  <MenuItem value="true">Yes</MenuItem>
                </Select>
                {errors.isFlagged && <FormHelperText>{errors.isFlagged}</FormHelperText>}
              </FormControl>
            </Box>
            {/* Acquisition Notes (100% width) */}
            <Box sx={{ flexBasis: '100%', flexGrow: 1 }}>
              <TextField
                margin="dense"
                label="Acquisition Notes (Optional)"
                name="acquisitionNotes"
                value={formData.acquisitionNotes}
                onChange={handleChange}
                fullWidth
                multiline
                rows={3}
                error={!!errors.acquisitionNotes}
                helperText={errors.acquisitionNotes || 'Optional: Any other details about purchase/item condition'}
                InputProps={{
                  endAdornment: <InfoTooltip text="Optional notes about the purchase, condition, or source. Example: Slight box damage, bought in auction." />,
                }}
              />
            </Box>
          </Box>
        </Box>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} color="secondary">
          Cancel
        </Button>
        {/* Ensure button triggers form submission via Box's onSubmit */}
         <Button type="submit" onClick={handleSubmit} variant="contained" color="primary">
          {isEditing ? 'Update Item' : 'Add Item'}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AddItemForm;