// --- Helper Functions ---
// Function to check if an item is late (Pending Delivery and Order Date > 7 days ago)
const isItemLate = (item: StockItem): boolean => {
  if (item.currentStatus !== 'Pending Delivery') {
    return false;
  }
  const orderDate = new Date(item.orderDate);
  const sevenDaysAgo = new Date();
  sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);
  return orderDate < sevenDaysAgo;
};

const getStatusStyle = (status: ItemStatus, isLate: boolean, isFlagged?: boolean): React.CSSProperties => { 
  // Basic style adjustments for flagged items (e.g., slightly bolder border or specific color)
  const flaggedStyle: React.CSSProperties = isFlagged ? { borderLeft: '3px solid red' } : {}; 
  
  let baseStyle: React.CSSProperties = {};
  if (status === 'Pending Delivery' && isLate) {
    baseStyle = { backgroundColor: '#cce5ff', color: '#004085' }; 
  } else {
    switch (status) {
      case 'Pending Delivery': baseStyle = { backgroundColor: '#fff3cd', color: '#856404' }; break;
      case 'Delivered': baseStyle = { backgroundColor: '#d4edda', color: '#155724' }; break;
      case 'Issue': baseStyle = { backgroundColor: '#f8d7da', color: '#721c24' }; break;
      case 'Archived': baseStyle = { backgroundColor: '#e2e3e5', color: '#383d41' }; break;
      default: baseStyle = {}; break;
    }
  }
  return { ...baseStyle, ...flaggedStyle }; 
};

// Function to create a new ActivityEvent
const createActivityEvent = (
    userId: string, 
    type: ActivityEventType,
    details: ActivityEvent['details'] = {}
): ActivityEvent => ({
    timestamp: new Date().toISOString(),
    type,
    userId, 
    details,
});

export const getAvailableActions = (status: ItemStatus, isFlagged?: boolean): string[] => {
  let actions: string[] = [];
  switch (status) {
    case 'Pending Delivery':
      actions = ['Mark as Delivered', 'Report Issue', 'View Details', 'Edit Item', 'Add Note']; break;
    case 'Delivered':
      actions = ['Archive', 'Report Issue', 'View Details', 'Edit Item', 'Add Note']; break;
    case 'Issue': 
      actions = ['Resolve Issue', 'Add Issue Update', 'Archive', 'View Details', 'Edit Item', 'Add Note']; break;
    case 'Late': 
      actions = ['Mark as Delivered', 'Report Issue', 'View Details', 'Edit Item', 'Add Note']; break;
    case 'Archived':
      actions = ['View Details']; break;
    default:
      actions = ['View Details']; break;
  }
  if (status !== 'Archived') {
    actions.unshift(isFlagged ? 'Unflag Item' : 'Flag Item');
  }
  return actions;
};

// Simple Action Menu Component
interface ActionMenuProps {
  itemId: string;
  actions: string[];
  onActionSelected: (itemId: string, action: string) => void;
  onClose: () => void;
}

export const ActionMenu: React.FC<ActionMenuProps> = ({ itemId, actions, onActionSelected, onClose }) => {
  const menuRef = useRef<HTMLDivElement>(null);

  // Close menu if clicked outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        onClose();
      }
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [onClose]);

  return (
    <div ref={menuRef} style={{
      position: 'absolute',
      backgroundColor: 'white',
      border: '1px solid #ccc',
      borderRadius: '4px',
      boxShadow: '0 2px 5px rgba(0,0,0,0.15)',
      zIndex: 10, 
      minWidth: '150px',
      marginTop: '2px' 
    }}>
      {actions.map(action => (
        <button
          key={action}
          onClick={() => onActionSelected(itemId, action)}
          style={{
            display: 'block',
            width: '100%',
            padding: '8px 12px',
            border: 'none',
            background: 'none',
            textAlign: 'left',
            cursor: 'pointer'
          }}
          onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = '#f0f0f0')}
          onMouseLeave={(e) => (e.currentTarget.style.backgroundColor = 'transparent')}
        >
          {action}
        </button>
      ))}
    </div>
  );
};

