// --- Draggable Header Component ---
interface DraggableHeaderProps {
  col: ColumnConfig;
}

const DraggableHeader: React.FC<DraggableHeaderProps> = ({ col }) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: col.id });

  const style: React.CSSProperties = {
    transform: CSS.Translate.toString(transform),
    transition,
    opacity: isDragging ? 0.8 : 1,
    zIndex: isDragging ? 10 : 1, 
    cursor: 'move',
    border: '1px solid #ccc',
    padding: '8px',
    textAlign: col.id === 'actions' ? 'center' : 'left',
    backgroundColor: isDragging ? '#e0e0e0' : '#f2f2f2' 
  };

  return (
    <th ref={setNodeRef} style={style} {...attributes} {...listeners}>
      {col.label}
    </th>
  );
};

// Update StockProcessingList props to accept onColumnReorder
const StockProcessingList = ({
  items,
  visibleColumns, 
  onActionSelected,
  onViewDetails,
  onColumnReorder, 
  selectedRowIndex = -1
}: {
  items: StockItem[];
  visibleColumns: ColumnConfig[];
  onActionSelected: (itemId: string, action: string) => void;
  onViewDetails: (itemId: string) => void;
  onColumnReorder: (event: DragEndEvent) => void; 
  selectedRowIndex?: number;
}) => {
  const [actionMenuAnchorEl, setActionMenuAnchorEl] = useState<null | HTMLElement>(null);
  const [currentItemIdForMenu, setCurrentItemIdForMenu] = useState<string | null>(null);

  const handleActionMenuClick = (event: React.MouseEvent<HTMLElement>, itemId: string) => {
    setActionMenuAnchorEl(event.currentTarget);
    setCurrentItemIdForMenu(itemId);
  };

  const handleActionMenuClose = () => {
    setActionMenuAnchorEl(null);
    setCurrentItemIdForMenu(null);
  };

  const handleActionMenuItemClick = (action: string) => {
    if (currentItemIdForMenu) {
      onActionSelected(currentItemIdForMenu, action);
    }
    handleActionMenuClose();
  };

  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  const columnIds = visibleColumns.map(col => col.id);

  return (
    <Paper sx={{ width: '100%', overflow: 'hidden' }}>
      <TableContainer sx={{ maxHeight: 'calc(100vh - 300px)' }}> 
        <DndContext
          sensors={sensors}
          collisionDetection={closestCenter}
          onDragEnd={onColumnReorder}
        >
          <Table stickyHeader aria-label="stock items table">
            <TableHead>
              <SortableContext items={columnIds} strategy={horizontalListSortingStrategy}>
                {visibleColumns.map((col) => (
                  <DraggableHeader key={col.id} col={col} />
                ))}
              </SortableContext>
            </TableHead>
            <TableBody>
              {items.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={visibleColumns.length} align="center">
                    No items found.
                  </TableCell>
                </TableRow>
              ) : (
                items.map((item, index) => {
                  const isLate = item.currentStatus === 'Pending Delivery' && isItemLate(item);
                  const statusStyle = getStatusStyle(item.currentStatus, isLate, item.isFlagged);
                  const availableActions = getAvailableActions(item.currentStatus, item.isFlagged);

                  return (
                    <TableRow
                      key={item.id}
                      hover
                      onClick={() => onViewDetails(item.id || '')} 
                      sx={{
                        cursor: 'pointer',
                        backgroundColor: index === selectedRowIndex ? theme => alpha(theme.palette.primary.light, 0.1) : 'inherit',
                        '&:last-child td, &:last-child th': { border: 0 },
                        ...statusStyle 
                      }}
                    >
                      {visibleColumns.map((col) => {
                        if (col.id === 'actions') {
                          return (
                            <TableCell key={col.id} align="right" onClick={(e) => e.stopPropagation()} sx={{ py: 0.5 }}>
                              {/* Replace button with IconButton and MUI Menu later */}
                               <IconButton 
                                  aria-label="actions"
                                  size="small"
                                  onClick={(e) => handleActionMenuClick(e, item.id || '')} 
                                >
                                  <MoreVertIcon fontSize="small" />
                                </IconButton>
                            </TableCell>
                          );
                        }
                        // Handle different data types for display
                        const cellValue = item[col.id as keyof StockItem];
                        let displayValue: React.ReactNode = cellValue;
                        if ((col.id as string) === 'orderDate' || (col.id as string) === 'deliveryDate') {
                          displayValue = cellValue ? new Date(cellValue as string).toLocaleDateString() : 'N/A';
                        } else if (typeof cellValue === 'boolean') {
                          displayValue = cellValue ? 'Yes' : 'No';
                        } else if (cellValue === null || cellValue === undefined || cellValue === '') {
                           displayValue = '-'; 
                        }
                        
                        return (
                          <TableCell key={col.id} sx={{ py: 1 }}> 
                            {displayValue}
                          </TableCell>
                        );
                      })}
                    </TableRow>
                  );
                })
              )}
            </TableBody>
          </Table>
        </DndContext>
      </TableContainer>
        {/* MUI Action Menu - Anchored to IconButton */} 
        <Menu
          anchorEl={actionMenuAnchorEl}
          open={Boolean(actionMenuAnchorEl)}
          onClose={handleActionMenuClose}
        >
          {currentItemIdForMenu && getAvailableActions(items.find(i => i.id === currentItemIdForMenu)?.currentStatus || 'Pending Delivery', items.find(i => i.id === currentItemIdForMenu)?.isFlagged).map((action) => (
            <MenuItem key={action} onClick={() => handleActionMenuItemClick(action)}>
              {action}
            </MenuItem>
          ))}
        </Menu>
    </Paper>
  );
};

