// --- Main Tool Component ---
const ToolApp: React.FC<ToolAppProps> = ({ user, org, permissions, logAction }) => {
  const [items, setItems] = useState<StockItem[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [currentFilter, setCurrentFilter] = useState<ItemStatus | 'All'>('All');
  const [searchTerm, setSearchTerm] = useState('');
  const [showAddItemForm, setShowAddItemForm] = useState(false); 
  // Column Visibility State
  const [visibleColumns, setVisibleColumns] = useState<ColumnConfig[]>(() => {
    // Initialize from localStorage or use default
    const saved = localStorage.getItem('visibleStockColumns');
    if (saved) {
        try {
            const savedIds = JSON.parse(saved) as string[];
            // Filter ALL_COLUMNS based on saved IDs to ensure correct structure
            const loadedColumns = ALL_COLUMNS.filter(col => savedIds.includes(col.id));
            // Ensure the order is also preserved if possible, or just use the filtered array
            // For simplicity here, we'll just use the filtered columns in their default order.
            // A more robust solution might store {id, order} in localStorage.
            return loadedColumns.length > 0 ? loadedColumns : DEFAULT_VISIBLE_COLUMN_IDS.map(id => ALL_COLUMNS.find(col => col.id === id)!);
        } catch (e) {
            console.error("Failed to parse saved columns:", e);
            return DEFAULT_VISIBLE_COLUMN_IDS.map(id => ALL_COLUMNS.find(col => col.id === id)!);
        }
    } 
    return DEFAULT_VISIBLE_COLUMN_IDS.map(id => ALL_COLUMNS.find(col => col.id === id)!);
  }); 
  const searchInputRef = useRef<HTMLInputElement | null>(null); 
  const [showHelpModal, setShowHelpModal] = useState(false); 
  const [showUserActivityView, setShowUserActivityView] = useState(false);
  const [selectedRowIndex, setSelectedRowIndex] = useState(-1); 

  // --- New Modal State --- 
  const [selectedItem, setSelectedItem] = useState<StockItem | null>(null);
  const [isDetailModalOpen, setIsDetailModalOpen] = useState(false);
  const [isAddUpdateModalOpen, setIsAddUpdateModalOpen] = useState(false);
  const [isResolveModalOpen, setIsResolveModalOpen] = useState(false);
  const [isReportIssueModalOpen, setIsReportIssueModalOpen] = useState(false); 
  const [isAddItemFormOpen, setIsAddItemFormOpen] = useState(false); 
  const [isSettingsModalOpen, setIsSettingsModalOpen] = useState(false); 
  const [isHelpModalOpen, setIsHelpModalOpen] = useState(false); 

  // Combine update and logging
  const updateItemAndLog = async (itemId: string, updates: Partial<StockItem>, actionDetails: Omit<LogActionData, 'timestamp' | 'userId' | 'orgId' | 'entityPath'>) => {
    if (!user || !org?.id || !logAction) {
      console.error("User, Org ID, or logAction function is not available.");
      setError("Cannot perform action: Missing user or configuration.");
      return;
    }
    const itemRef = doc(db, `orgs/${org.id}/stockItems`, itemId);
    const itemToUpdate = items.find(i => i.id === itemId);

    try {
      const updateData = removeUndefinedFields(updates);
      await updateDoc(itemRef, {
        ...updateData,
        lastModified: serverTimestamp(),
        lastModifiedBy: user.uid 
      });

      // Prepare log data
      const logData: LogActionData = {
        userId: user.uid, 
        orgId: org.id,
        entityPath: `stockItems/${itemId}`,
        timestamp: serverTimestamp(), 
        ...actionDetails, 
        before: actionDetails.action.startsWith('item.update') ? removeUndefinedFields(itemToUpdate || {}) : undefined, 
        after: actionDetails.action.startsWith('item.update') ? removeUndefinedFields(updateData) : undefined 
      };

      await logAction(logData);
      console.log(`Action logged: ${actionDetails.action} for item ${itemId}`);

    } catch (err) {
      console.error('Error updating item or logging action:', err);
      setError(`Failed to ${actionDetails.action.replace('item.', '')}. Please try again.`);
    } finally {
       // Optionally close relevant modals here if the action implies modal completion
       // Example: if (actionDetails.action === 'item.update.issue.resolve') handleCloseResolveModal();
       // This might be better handled within the specific submit handlers (handleResolveIssue etc.)
    }
  };

  // Fetch items from Firestore on component mount and when org changes
  useEffect(() => {
    // ... rest of the code remains the same ...
  }, [org]);

  // --- DND Handlers ---
  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  const handleDragEnd = useCallback((event: DragEndEvent) => {
    const { active, over } = event;

    if (active.id !== over?.id) {
      setVisibleColumns((cols) => {
        const oldIndex = cols.findIndex(col => col.id === active.id);
        const newIndex = cols.findIndex(col => col.id === over?.id);
        // Use arrayMove for immutable reordering
        return arrayMove(cols, oldIndex, newIndex);
      });
    }
  }, []);

  // --- Modal Close Handlers ---
  const handleCloseDetailModal = () => {
    setIsDetailModalOpen(false);
    setSelectedItem(null);
  };
  const handleCloseAddUpdateModal = () => {
    setIsAddUpdateModalOpen(false);
    setSelectedItem(null);
  };
  const handleCloseResolveModal = () => {
    setIsResolveModalOpen(false);
    setSelectedItem(null);
  };
  const handleCloseReportIssueModal = () => { 
    setIsReportIssueModalOpen(false);
    setSelectedItem(null);
  };
  const handleCloseAddItemForm = () => { 
    setIsAddItemFormOpen(false);
    setSelectedItem(null); 
  };
  const handleCloseSettingsModal = () => { 
    setIsSettingsModalOpen(false);
  };
   const handleCloseHelpModal = () => { 
    setIsHelpModalOpen(false);
  };
  // --- End Modal Close Handlers ---

  // Handler for adding a new item
  const handleAddItem = async (newItemData: Omit<StockItem, 'id' | 'activityLog' | 'lastModified' | 'lastModifiedBy' | 'isFlagged' | 'currentStatus' | 'isLate' | 'orderDate'> & { orderDate: string }) => {
    if (!user || !org?.id || !logAction) {
      console.error("User, Org ID, or logAction function is not available.");
      setError("Cannot perform action: Missing user or configuration.");
      return;
    }
    const docRef = await addDoc(collection(db, `orgs/${org.id}/stockItems`), {
      ...newItemData,
      currentStatus: 'Pending Delivery',
      isFlagged: false,
      lastModified: serverTimestamp(),
      lastModifiedBy: user.uid,
      activityLog: [createActivityEvent(user.uid, 'CREATED')],
    });

    // Prepare log data for central logging service
    const logData: LogActionData = {
      userId: user.uid, 
      orgId: org.id,
      toolKey: 'incoming-tool',
      entityPath: `stockItems/${docRef.id}`,
      action: 'item.create',
      timestamp: serverTimestamp(), 
    };

    await logAction(logData);
    console.log(`Action logged: item.create for item ${docRef.id}`);

    // setShowAddItemForm(false); 
    handleCloseAddItemForm();
  };

  // Handler for updating an existing item (e.g., from Edit form)
  const handleUpdateItem = async (itemId: string, updatedData: Partial<Omit<StockItem, 'id' | 'activityLog' | 'lastModified' | 'lastModifiedBy' | 'isLate'>>) => {
    // Find the original item to compare changes for logging
    const originalItem = items.find(item => item.id === itemId);
    if (!originalItem) {
        setError("Could not find item to update.");
        return;
    }

    await updateItemAndLog(itemId, updatedData, {
      toolKey: 'incoming-tool', 
      action: 'item.update.fields',
      details: { 
          // Calculate changed fields dynamically if possible, or provide a generic message
          // changedFields: Object.keys(updatedData).filter(key => originalItem[key] !== updatedData[key]) 
          updatedFields: Object.keys(updatedData)
      }
    });
    // Potentially close the edit form if it's open
    handleCloseAddItemForm(); 
  };

  // Handler for actions selected from the ActionMenu or elsewhere
  const handleActionSelected = (itemId: string, action: string) => {
    const item = items.find(i => i.id === itemId);
    if (!item) return;

    setSelectedItem(item); 

    switch (action) {
      case 'View Details':
        setIsDetailModalOpen(true);
        // setShowDetailModal(true); 
        break;
      case 'Edit Item':
        // setSelectedItem(item); 
        setIsAddItemFormOpen(true); 
        break;
      case 'Add Issue Update':
        // setSelectedItem(item); 
        setIsAddUpdateModalOpen(true);
        break;
      case 'Resolve Issue':
        // setSelectedItem(item); 
        setIsResolveModalOpen(true);
        break;
      case 'Report Issue':
        // setSelectedItem(item); 
        setIsReportIssueModalOpen(true);
        break;
      case 'Flag Item':
      case 'Unflag Item':
        updateItemAndLog(itemId, { isFlagged: action === 'Flag Item' }, {
            toolKey: 'incoming-tool', 
            action: 'item.update.flag',
            details: { flagged: action === 'Flag Item' }
        });
        setSelectedItem(null); 
        break;
      case 'Mark as Delivered':
        updateItemAndLog(itemId, { 
            currentStatus: 'Delivered',
            dateDelivered: new Date().toISOString() 
        }, {
            toolKey: 'incoming-tool', 
            action: 'item.update.status.Delivered',
            details: { previousStatus: item.currentStatus }
        });
        setSelectedItem(null);
        break;
      case 'Archive':
        updateItemAndLog(itemId, { currentStatus: 'Archived' }, {
            toolKey: 'incoming-tool', 
            action: 'item.update.status.Archived',
            details: { previousStatus: item.currentStatus }
        });
        setSelectedItem(null);
        break;
      // Add cases for 'Add Note' etc. if implemented
      default:
        console.warn(`Unhandled action: ${action}`);
        setSelectedItem(null); 
        break;
    }
  };

  // Handler for viewing item details (can be triggered from StockProcessingList)
  const handleViewDetails = (itemId: string) => {
    const item = items.find(i => i.id === itemId);
    if (item) {
      setSelectedItem(item);
      setIsDetailModalOpen(true);
    }
  };

  // Handler for reporting an issue (called by ReportIssueModal onSubmit)
  const handleReportIssue = async (itemId: string, issueDescription: string) => {
      await updateItemAndLog(itemId, { 
          currentStatus: 'Issue', 
          issueDescription: issueDescription 
      }, {
          toolKey: 'incoming-tool', 
          action: 'item.update.issue.report',
          details: { description: issueDescription, previousStatus: items.find(i => i.id === itemId)?.currentStatus }
      });
      handleCloseReportIssueModal(); 
  };

  // Handler for saving an issue update (called by AddUpdateModal onSubmit)
  const handleSaveIssueUpdate = async (note: string) => {
    if (!selectedItem?.id) return;
    // We just log the update; the item's core fields don't necessarily change
    await updateItemAndLog(selectedItem.id, {}, { 
      toolKey: 'incoming-tool', 
      action: 'item.update.issue.update',
      details: { note: note }
    });
    handleCloseAddUpdateModal(); 
  };

  // Handler for resolving an issue (called by ResolveIssueModal onSubmit)
  const handleResolveIssue = async (outcome: string, note?: string) => {
    if (!selectedItem?.id) return;
    // Change status back to Delivered (or Pending if appropriate? TBD) and clear issue description
    await updateItemAndLog(selectedItem.id, { 
        currentStatus: 'Delivered', 
        issueDescription: undefined, 
        // Potentially update processorNotes based on resolution?
    }, {
        toolKey: 'incoming-tool', 
        action: 'item.update.issue.resolve',
        details: { resolutionOutcome: outcome, note: note, previousStatus: selectedItem.currentStatus }
    });
    handleCloseResolveModal(); 
  };

  // Handle saving column settings from modal
  const handleSaveSettings = (newVisibleColumnIds: string[]) => {
    // Persist settings (e.g., localStorage)
    localStorage.setItem('visibleStockColumns', JSON.stringify(newVisibleColumnIds));
    // Update state based on new IDs
    setVisibleColumns(ALL_COLUMNS.filter(col => newVisibleColumnIds.includes(col.id)));
    // setShowSettingsModal(false); 
    handleCloseSettingsModal();
  };

  const handleToggleUserActivityView = () => {
    // ... rest of the code remains the same ...
  };

  // --- Placeholder Handlers for UserActivityView --- 
  const handleActivityEntityClick = (entityPath: string) => {
    console.log("User Activity View - Entity Clicked:", entityPath);
    // Potentially find and open the corresponding item detail modal
    const itemId = entityPath.split('/')[1]; 
    if (itemId) {
        handleViewDetails(itemId);
    }
  };
  const handleActivityActionSelected = (itemId: string, action: string) => {
    console.log("User Activity View - Action Selected:", itemId, action);
    // Potentially trigger the action
    handleActionSelected(itemId, action);
  };
  // --- End Placeholder Handlers ---

  const filteredItems = items
    .map(item => ({ ...item, isLate: isItemLate(item) })) 
    .filter(item => {
      // ... rest of the code remains the same ...
    });

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      {/* Error Display */} 
      {error && (
        <Box sx={{ mb: 2, p: 2, backgroundColor: '#f8d7da', color: '#721c24', borderRadius: 1 }}>
          {error}
        </Box>
      )}
      {/* Search Bar */}
      <SearchBar onSearch={setSearchTerm} inputRef={searchInputRef} />
      {/* Filter Tabs */}
      <FilterTabs currentFilter={currentFilter} onFilterChange={setCurrentFilter} />
      {/* Add Item Button */}
      <AddItemButton onClick={() => setIsAddItemFormOpen(true)} />
      {/* Flag Toggle */}
      <FlagToggle isChecked={false} onChange={() => console.log('Flag toggle changed')} />
      {/* Stock Processing List */}
      <StockProcessingList
        items={filteredItems}
        visibleColumns={visibleColumns} 
        onActionSelected={handleActionSelected}
        onViewDetails={handleViewDetails} 
        onColumnReorder={handleDragEnd} 
        selectedRowIndex={selectedRowIndex} 
      />

      {/* Render Modals */} 
      {/* Ensure AddItemForm is refactored to accept 'open' and 'itemToEdit' props */}
      {isAddItemFormOpen && user && (
        <AddItemForm
          open={isAddItemFormOpen} 
          itemToEdit={selectedItem} 
          onSubmit={(data: any) => selectedItem ? handleUpdateItem(selectedItem.id || '', data) : handleAddItem}
          onClose={handleCloseAddItemForm}
          user={user} 
          orgId={org?.id} 
        />
      )}

      {/* Refactored Modals */} 
      <ItemDetailModal 
        open={isDetailModalOpen}
        item={selectedItem}
        orgId={org?.id}
        onClose={handleCloseDetailModal}
        onActionSelected={handleActionSelected} 
      />

      <AddUpdateModal
        open={isAddUpdateModalOpen}
        item={selectedItem}
        onClose={handleCloseAddUpdateModal}
        onSubmit={handleSaveIssueUpdate} 
      />

      <ResolveIssueModal
        open={isResolveModalOpen}
        item={selectedItem}
        onClose={handleCloseResolveModal}
        onSubmit={handleResolveIssue}
      />

      {/* Placeholder for ReportIssueModal - refactor needed */}
      {/* <ReportIssueModal open={isReportIssueModalOpen} item={selectedItem} onClose={handleCloseReportIssueModal} onSubmit={handleReportIssue} /> */}
      {isReportIssueModalOpen && selectedItem && (
          <ReportIssueModal 
              item={selectedItem} 
              onSubmit={(desc) => handleReportIssue(selectedItem.id, desc)} 
              onClose={handleCloseReportIssueModal} 
          />
      )}

      {/* Placeholder for TableSettingsModal - refactor needed */}
      {/* <TableSettingsModal open={isSettingsModalOpen} ... /> */}
      {isSettingsModalOpen && (
          <TableSettingsModal
              isOpen={isSettingsModalOpen} 
              allColumns={ALL_COLUMNS}
              initialVisibleColumns={visibleColumns} 
              onSave={handleSaveSettings}
              onClose={handleCloseSettingsModal} 
          />
      )}

      {/* Placeholder for HelpModal - refactor needed */}
      {/* <HelpModal open={isHelpModalOpen} onClose={handleCloseHelpModal} /> */}
      {isHelpModalOpen && <HelpModal onClose={handleCloseHelpModal} />}

      {/* User Activity View - Assuming this is already a standalone component/page */}
      {showUserActivityView && <UserActivityView 
          orgId={org?.id} 
          onClose={handleToggleUserActivityView} 
          onEntityClick={handleActivityEntityClick} 
          onActionSelected={handleActivityActionSelected} 
       />}
    </Container>
  );
};

export default ToolApp;